  font-display: swap;
  src: local(""), url("../fonts/roboto/roboto-cyrillic-500.woff2") format("woff2"), url("../fonts/roboto/roboto-cyrillic-500.woff") format("woff");
  /* Chrome 6+, Firefox 3.6+, IE 9+, Safari 5.1+ */ }

