:root {
  --content-width: 1336px;
  --gutter: 12px;
  --mob-padding: 20px; }

.container {
  max-width: calc(var(--content-width));
  padding: 0 var(--mob-padding);
  margin: 0 auto; }

.row {
  display: flex;
  flex-wrap: wrap;
  margin-left: calc(var(--gutter) * -1);
  margin-right: calc(var(--gutter) * -1); }

* {
  box-sizing: border-box; }

html {
  font-family: "Roboto"; }

body {
  display: flex;
  flex-direction: column;
  min-height: 100vh; }

img {
  display: block;
  max-width: 100%; }

.scroll-hidden {
  overflow: hidden; }

.header {
  background-color: blue;
  color: #fff; }

.main {
  flex-grow: 1; }

.footer {
  flex-shrink: 0;
  background-color: blue;
  color: #fff; }
